"use client";

import { useState } from "react";
import ImageUpload from "./components/ImageUpload";
import PreviewCanvas from "./components/PreviewCanvas";

export default function Home() {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [description, setDescription] = useState("");
  const [viewAngle, setViewAngle] = useState("perspective");
  const [style, setStyle] = useState("realistic");
  const [isGenerating, setIsGenerating] = useState(false);
  const [resultUrl, setResultUrl] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleGenerate = async () => {
    if (!selectedFile) return;

    setIsGenerating(true);
    setError(null);
    setResultUrl(null);

    const formData = new FormData();
    formData.append("image", selectedFile);
    formData.append("description", description);
    formData.append("viewAngle", viewAngle);
    formData.append("style", style);

    try {
      const response = await fetch("http://localhost:8000/api/generate", {
        method: "POST",
        body: formData,
      });

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.detail || "ÁîüÊàêÂ§±Ë¥•");
      }

      if (data.success && data.imageUrl) {
        setResultUrl(`http://localhost:8000${data.imageUrl}`);
      } else {
        throw new Error("APIÊú™ËøîÂõûÂõæÁâáÈìæÊé•");
      }
    } catch (err: any) {
      console.error(err);
      setError(err.message || "ËØ∑Ê±ÇÂèëÁîüÈîôËØØÔºåËØ∑ÈáçËØï");
    } finally {
      setIsGenerating(false);
    }
  };

  const handleDownload = () => {
    if (resultUrl) {
      const link = document.createElement('a');
      link.href = resultUrl;
      link.download = `blueprint3d-${Date.now()}.png`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  return (
    <div className="container">
      <header className="header">
        <div className="logo">Blueprint3D</div>
        <div className="text-secondary">v1.0</div>
      </header>

      <main className="main-layout">
        {/* Left Column: Upload + Description + Generate */}
        <aside className="panel">
          <ImageUpload
            onImageSelect={setSelectedFile}
            selectedImage={selectedFile}
          />

          <div className="form-group">
            <label className="label">2. Ë°•ÂÖÖÊèèËø∞ (ÂèØÈÄâ)</label>
            <textarea
              className="textarea"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="‰æãÂ¶ÇÔºöËøôÊòØ‰∏ÄÂº†Èí¢ÁªìÊûÑÂéÇÊàøÁöÑÂπ≥Èù¢ÂõæÔºåÂ±ãÈ°∂ÊúâÂ§©Á™ó..."
            />
          </div>

          <div style={{ marginTop: 'auto' }}>
            {error && <div className="error-message" style={{ marginBottom: '1rem' }}>{error}</div>}

            <button
              className="button"
              onClick={handleGenerate}
              disabled={!selectedFile || isGenerating}
            >
              {isGenerating ? "Ê≠£Âú®ÁîüÊàê..." : "ÁîüÊàê 3D ÊïàÊûúÂõæ"}
            </button>
          </div>
        </aside>

        {/* Center Column: Preview */}
        <section className="panel" style={{ background: 'white', flex: 1, padding: '1rem', overflow: 'hidden' }}>
          <PreviewCanvas
            resultUrl={resultUrl}
            isGenerating={isGenerating}
            onDownload={handleDownload}
          />
        </section>

        {/* Right Column: Settings */}
        <aside className="panel">
          <h3 className="label" style={{ fontSize: '1.2rem', marginBottom: '0.5rem' }}>ÁîüÊàêËÆæÁΩÆ</h3>

          <div className="form-group">
            <label className="label">3. ËßÜËßíÈÄâÊã©</label>
            <div className="text-xs text-secondary mb-2">ÈÄâÊã©ÁîüÊàêÂõæÂÉèÁöÑÁõ∏Êú∫ËßÜËßí</div>
            <select
              className="select"
              value={viewAngle}
              onChange={(e) => setViewAngle(e.target.value)}
            >
              <option value="perspective">ÈÄèËßÜÂõæ (Perspective)</option>
              <option value="top">‰øØËßÜÂõæ (Top View)</option>
              <option value="front">Ê≠£ËßÜÂõæ (Front View)</option>
              <option value="side">‰æßËßÜÂõæ (Side View)</option>
            </select>
          </div>

          <div className="form-group">
            <label className="label">4. È£éÊ†ºÈÄâÊã©</label>
            <div className="text-xs text-secondary mb-2">ÈÄâÊã©Ê∏≤ÊüìÁöÑËâ∫ÊúØÈ£éÊ†º</div>
            <select
              className="select"
              value={style}
              onChange={(e) => setStyle(e.target.value)}
            >
              <option value="realistic">ÂÜôÂÆûÊ∏≤Êüì (Realistic)</option>
              <option value="technical">ÊäÄÊúØÁ∫øÁ®ø (Technical)</option>
              <option value="cartoon">ÁÆÄÁ∫¶Âç°ÈÄö (Cartoon)</option>
            </select>
          </div>

          <div className="mt-auto p-4 bg-blue-50 rounded-lg text-sm text-blue-800" style={{ background: '#eff6ff', color: '#1e40af' }}>
            <strong>üí° ÊèêÁ§∫:</strong>
            <p className="mt-1">‰∏ä‰º†Ê∏ÖÊô∞ÁöÑÂπ≥Èù¢ÂõæÔºåÂπ∂Âú®ÊèèËø∞‰∏≠Ë°•ÂÖÖÊùêË¥®ÊàñÈ¢úËâ≤‰ø°ÊÅØÔºåÊïàÊûú‰ºöÊõ¥Â•Ω„ÄÇ</p>
          </div>
        </aside>
      </main>
    </div>
  );
}
